import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';
import '../../models/order';

import { baseUrl } from '../../config/variables';

@Injectable({
  providedIn: 'root',
})
export class OrdersService {
  constructor(private http: HttpClient) {}

  orders = new BehaviorSubject(null);

  getAll() {
    return this.http.get(baseUrl + '/order').subscribe((res) => {
      if (res['data']) {
        this.orders.next(res['data']);
      }
    });
  }

  confirmOne(itemId) {
    return this.http.delete(baseUrl + `/order/${itemId}`);
  }

  shipOne(itemId) {
    return this.http.get(baseUrl + `/order/${itemId}`);
  }

  deliverOne(form) {
    return this.http.post(baseUrl + '/order', form);
  }
}
